import {
    ADD_COUNTER,
    ADD_TO_CART
} from './mutation-type'


export default{
    addCart(context,payload){
        return new Promise((resolve,reject)=>{
            let oldProduct = null
            for(let item of context.state.cartList){
                if(item.iid === payload.iid){//已收藏
                    oldProduct = item
                }
            }
            if(oldProduct){
                // oldProduct.count += 1
                // console.log("11111");
                context.commit(ADD_COUNTER,oldProduct)
                resolve('当前的商品数量+1')
            }else{
                // payload.count = 1
                // context.state.cartList.push(payload)
                // console.log("22222");
                context.commit(ADD_TO_CART,payload)
                resolve("添加了新的商品")
            }
        })
    }
}