<template>
  <div class="toast" v-show="isShow">
      <div >{{message}}</div>
  </div>
</template>

<script>
export default {
    props:{
        // message:{
        //     type:String,
        //     default:''
        // },
        // show:{
        //     type:Boolean,
        //     default:false
        // }
    },
    data(){
        return {
            message:'',
            isShow:false
        }
    },
    methods:{
        show(message="默认文字",duration=2000){
            this.message=message
            this.isShow = true

            setTimeout(()=>{
                this.message=""
                this.isShow = false
            },duration)
        }
    }
}
</script>

<style>
    .toast{
         position: fixed;
         top: 50%;
         left: 50%;
         color: white;
         padding: 8px 10px;
         border-radius: 5px;
         transform: translate(-50%,-50%);
         font-size: 15px;
        z-index: 999;

         background-color: rgba(92, 92, 92,.8);
    }
</style>